.btn {
    color: #757575;
    font-size: 20px;
    background: transparent;
    border: 1px solid #757575;
    width: 150px;
    height: 40px;
    position:relative;
    left: 50%;
    top: 50%;
    text-align: center;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    -webkit-transition: all .6s ease-in-out;
    transition: all .6s ease-in-out;
    overflow: hidden;
}


.btn::before {
    content: '';
    position: absolute;
    top: -130px;
    margin: auto;
    display: block;
    border-radius: 50%;
    width: 300px;
    height: 300px;
    left: -75px;
    -webkit-transition: box-shadow .6s ease-in-out;
    transition: box-shadow .6s ease-in-out;
    z-index: -1;

}

.btn:hover {
    color: #fff;
}

.btn:hover::before {
    box-shadow: inset 0 0 0 160px #3aa251;
}
